import {slide as Menu} from 'react-burger-menu'
import React, { Component } from 'react';
class MenuHamburguer extends Component {
  render() {
    var adminLink = ""
    if (this.props.user.is_staff) {
      adminLink = <li><a onClick={this.props.admin}><i className="glyphicon glyphicon-flag"></i>Administrao </a></li>
    }
    return(
<Menu pageWrapId={this.props.pageWrapId} isOpen={this.props.isOpen} onStateChange={ this.props.onStateChange }customBurgerIcon={ false } customCrossIcon={ false } right>
  <div className="profile-sidebar outer-container">
    <div className="profile-userpic">
    	<img src="http://www.faimcs.com/images/profile-default-male.jpg" className="img-responsive" alt=""></img>
    </div>

    <div className="profile-usertitle">
    	<div className="profile-usertitle-name">
    		{this.props.user.username}
    	</div>
    	<div className="profile-usertitle-job">
    		{this.props.user.email}
    	</div>
    </div>
    <div className="profile-usermenu">
    	<ul className="nav">
    		<li>
    			<a href="#">
    			<i className="glyphicon glyphicon-user"></i>
    			Configurao da Conta </a>
    		</li>
    		<li>
    			<a href="#">
    			<i className="glyphicon glyphicon-flag"></i>
    			Ajuda </a>
    		</li>
        {adminLink}

        <li>
          <a href="#">
          <i className="glyphicon glyphicon-remove"></i>
          Sair </a>
        </li>
    	</ul>
    </div>
  </div>
</Menu>
    )
  }
}

export default MenuHamburguer;



