import React, { Component } from 'react';

class TopIcons extends Component {
  render() {
    console.log(this.props.active)
    if (this.props.active === this.props.name) {
      return(
            <li className="active" onClick={this.props.handleClick}><a><i className={this.props.icon} aria-hidden="true"></i></a></li>
      )
    } else {
        return(<li><a><i onClick={this.props.handleClick} className={this.props.icon} aria-hidden="true"></i></a></li>)
    }
  }


}

class Top extends Component {
  constructor(props) {
    super(props)
    this.state = {
                    active:'home'
                  }
  }

  changePageHome() {
    this.props.onPageChange('home')
  }

  render() {
    return (
<nav className="navbar navbar-default">
  <div className="container-fluid">
    <div className="navbar-header">
      <a className="navbar-brand"><i className="fa fa-futbol-o fa-lg" aria-hidden="true"></i> Footline</a>
    </div>

    <div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
      <ul className="nav navbar-nav">
        <TopIcons active={this.props.pageview} name="home" icon="fa fa-home fa-lg" handleClick={() => this.props.onPageChange('home')}/>
        <TopIcons active={this.props.pageview} name="users" icon="fa fa-users fa-lg"/>
      </ul>
      <ul className="nav navbar-nav navbar-right">
        <li><a onClick={this.props.openMenu}><i className="fa fa-bars" aria-hidden="true"></i></a></li>
      </ul>
    </div>
  </div>
</nav>
    )
  }
}

export default Top;



