import React from 'react'
import axios from 'axios'
import FontAwesome from 'react-fontawesome'


class RegisterToken extends React.Component{
  constructor(props) {

    super(props);
    this.state = {'token':{'id': props.id, 'token': props.token, 'use': props.use}};
    this.updateItem = this.updateItem.bind(this)
  }

  addItem(data) {
    var arrayvar = this.state.registerTokens.slice()
    arrayvar.splice(0, 0, data)
    this.setState({ registerTokens: arrayvar })
  }

  updateItem(index, data) {
    var newData = this.state.registerTokens.slice(); //copy array
    newData.splice(index, 1, data);
    this.setState({registerTokens: newData}); //update state
  }

  treatUpdate(data) {
    var that = this;
    this.state.registerTokens.forEach(function(registertoken, i) {
      if (registertoken.id === data.id) {
          that.updateItem(i, data)
      }
    })
  }

  handleData(data) {
    var identifier = data.split(':')
    var that = this;
    if (identifier[0] === 'rt') {
      if(identifier[1] === 'add') {
        axios.get('https://footline.herokuapp.com/api/admin/registertoken/'+ identifier[2] +'/').then(function(response) {
            that.addItem(response.data);
        });
      }
      if(identifier[1] === 'update') {
        axios.get('https://footline.herokuapp.com/api/admin/registertoken/'+ identifier[2] +'/').then(function(response) {
            that.treatUpdate(response.data);
        });
      }
    }
  }

  componentDidMount() {
    var url = 'https://footline.herokuapp.com'
    var self;

    self = this;


    function populateRegiterTokens(tokens) {
      self.setState({registerTokens:tokens});
    };



    function getData() {
      axios.get(url + '/api/admin/registertoken/',{}).then(function (response) {
        populateRegiterTokens(response['data'])
      }).catch(function (error) {
        console.log(error);
      });
    };

    getData();

  };

 render() {
   function iconize(value) {
     if (value) {
       return <FontAwesome name='ban' />;
     }
      else {
        return <FontAwesome name='check' />;
      }
   }

   var listRegisterTokens = "";
   if (this.state.registerTokens) {
     listRegisterTokens = this.state.registerTokens.map((token) =><tr key={token.id}><td>{token.token}</td><td>{iconize(token.use)}</td></tr>)
   }

   return(
     <div className="col-md-10">
       <h3>Tokens para registro</h3>
       <table className="table table-bordered">
         <thead className="thead thead-style" >
           <tr>
             <th>Token</th>
             <th>Disponivel</th>
           </tr>
         </thead>
         <tbody id="tbody_users">
          {listRegisterTokens}
         </tbody>
       </table>
     </div>
   )
 }
};

export default RegisterToken;



