import React, { Component } from 'react';
import axios from 'axios'
import Top from './Top.js'
import MenuHamburguer from './MenuHamburguer.js'
import BodyHome from './BodyHome.js'
import RegisterToken from './admin/RegisterToken.js'
import NotificationSystem from 'react-notification-system';


class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
                   token: 'bd3a7862e79db437beb0bc7d6aeef709179e2b83',
                   menu: false,
                   pageview: 'home',
                  }
    this.login = this.login.bind(this)
    this.openmenu = this.openmenu.bind(this)
    this.closemenu = this.closemenu.bind(this)
    this.isMenuOpen = this.isMenuOpen.bind(this)
    this.turnAdmin = this.turnAdmin.bind(this)
    this.onPage = this.onPage.bind(this)
    this.handleSocketMessages = this.handleSocketMessages.bind(this)
    this.handleNotification = this.handleNotification.bind(this)
    this.notificationSystem = null;
    this.testNotification = this.testNotification.bind(this)
  }


  login(data) {
    this.setState({user: data})
  }


  handleSocketMessages(message) {
    var code = message.split(":")
    if (code[0] === "not") {
      this.handleNotification(code[1])
    }
  }

  handleNotification(message) {
    var notification = {
      title: 'Mensagem do Servidor',
      message: message,
      level: 'success',
      position: 'bc',
      dismissible: false,
      autoDismiss: 5
    }
    this.notificationSystem.addNotification(notification)
  }

  componentDidMount() {
    var that = this;

    var config = {
                    headers: {'Authorization': 'Token '+this.state.token}
                  };

    var socket = new WebSocket("wss://footline.herokuapp.com/")

    socket.onmessage = function(e) {
        this.handleSocketMessages(e.data);
    }

    axios.get('https://footline.herokuapp.com/api/v1/auth/', config).then(function(response) {
      that.login(response.data)
    }).catch(function(error) {
      window.location = 'https://footline.herokuapp.com/'
    })

    socket.onopen = function() {
        socket.send('token:'+ that.state.token);
    }
    // Call onopen directly if socket is already open
    if (socket.readyState === WebSocket.OPEN) socket.onopen();

  }

  testNotification() {
    this.handleNotification('teste')
  }

  openmenu() {
    this.setState({menu: !this.state.menu})
  }

  closemenu() {
    this.setState({menu: false})
  }

  isMenuOpen(state) {
    this.setState({menu: state.isOpen})
  }

  turnAdmin() {
    this.setState({menu: false})
    this.setState({pageview: 'admin'})
  }

  onPage(e) {
    this.setState({pageview: e})
  }

  render() {
    if (this.state.user) {
      var body = ""
      if (this.state.pageview === 'home') {
        body =<BodyHome />
      }
      if (this.state.pageview === 'admin') {
        body=<RegisterToken />
      }
      return (
                <div className="outer-container">
                  <MenuHamburguer pageWrapId={ "page-wrap" } user={ this.state.user } isOpen={ this.state.menu } width={280} onStateChange={ this.isMenuOpen } admin={ this.turnAdmin }/>
                  <main id="page-wrap">
                    <Top openMenu={this.openmenu} pageview={this.state.pageview} onPageChange={this.onPage}/>
                    {body}
                  </main>
                  <a onClick={this.testNotification}>Testa notificao</a>
                  <NotificationSystem ref={n => this.notificationSystem = n} />
                </div>
              )
    }
    else {
      return(null)
    }
  }
}

export default App;



